import { html } from 'lit';
import '@shoelace-style/shoelace/dist/components/radio-group/radio-group.js';
import '@shoelace-style/shoelace/dist/components/radio-button/radio-button.js';
export const buttonGroup = (options) => html `
	<sl-radio-group
		size="medium"
		.label=${options.label ?? ''}
		.helpText=${options.helpText ?? ''}
		value=${typeof options.value !== 'undefined' ? String(options.value) : ''}
		.name=${options.id}
		.required=${options.required ?? false}
		@sl-change=${(event) => {
    let newValue = event.target.value;
    if (options.type === 'number') {
        newValue = Number(newValue);
    }
    options.valueChangedCallback?.(newValue);
}}
	>
		${options.enum?.map((enumVal) => html `<sl-radio-button
					.disabled=${String(enumVal) === options.value ? false : options.disabled}
					value=${String(enumVal)}
					>${enumVal}</sl-radio-button
				>`)}
	</sl-radio-group>
`;
