import { html } from 'lit';
import '@shoelace-style/shoelace/dist/components/radio-group/radio-group.js';
import '@shoelace-style/shoelace/dist/components/radio/radio.js';
export const radioGroup = (options) => html `
	<sl-radio-group
		class="theme-shoelace widget-radio-group"
		size="medium"
		.label=${options.label ?? ''}
		.helpText=${options.helpText ?? ''}
		value=${typeof options.value !== 'undefined' ? String(options.value) : ''}
		.name=${options.id}
		.required=${options.required ?? false}
		@sl-change=${(event) => {
    let newValue = event.target.value;
    if (options.type === 'number') {
        newValue = Number(newValue);
    }
    options.valueChangedCallback?.(newValue);
}}
	>
		${options.enum?.map((enumVal) => html `<sl-radio .disabled=${options.disabled} value=${String(enumVal)}
					>${enumVal}</sl-radio
				>`)}
	</sl-radio-group>
`;
