import { html } from 'lit';
import '@shoelace-style/shoelace/dist/components/radio-group/radio-group.js';
import '@shoelace-style/shoelace/dist/components/radio-button/radio-button.js';
export const buttonGroupBoolean = (options) => html `
	<sl-radio-group
		size="medium"
		.label=${options.label ?? ''}
		.helpText=${options.helpText ?? ''}
		value=${typeof options.value !== 'undefined' ? String(options.value) : ''}
		.name=${options.id}
		.required?=${options.required}
		@sl-change=${(event) => {
    const newValue = event.target.value;
    options.valueChangedCallback?.(newValue === 'true' ?? newValue !== 'false' ?? undefined);
}}
	>
		<sl-radio-button
			value="true"
			.disabled=${options.value === true ? false : options.disabled}
			>${options.trueLabel ?? 'Yes'}</sl-radio-button
		>
		<sl-radio-button
			value="false"
			.disabled=${options.value === false ? false : options.disabled}
			>${options.falseLabel ?? 'No'}</sl-radio-button
		>
	</sl-radio-group>
`;
